@import url('https://fonts.googleapis.com/css2?family=Caveat+Brush&family=Montserrat&display=swap');

.NavbarWrapper {
    width : 100vw;
    height: 15vh;
    background-color: white;
    top: 0;
    display: grid;
    position: fixed;
    z-index: 999;
    grid-template-columns: repeat(4,1fr);
    list-style: none;
    grid-gap : 1vh;
    margin: 0;
    padding: 0;
    .link {
        text-decoration: none;
        color : #81818181;
        transition:1s;
    }
    .linkActive {
        color : rgb(33, 100, 226);
    }
    .logo {
        display: flex;
        font-family: 'Caveat Brush',cursive;
        color : rgb(33, 100, 226);
        img {
            width : 7vw;
            height : 7vw;
        }
        @media screen and (min-width : 3000px){
            img {
                transform: scalex(1);
            }
            padding-left : 4vw;
            h1 {
                width : 2vw;
                padding-left : 2vw;
            }
        }
    }
}
