@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Caveat+Brush&family=Montserrat&display=swap');
.NoteArea {
    position: relative;
    animation: appear 2s;
    transition: 1s;
    .wrapper {
        width : 17vw;
        background-color: rgb(250, 249, 249);
        border : solid grey 1px;
        transition: 1s;

        .text {
            display: flex;
            justify-content: center;
        }
    }
    h1 {
        max-width : 15.5vw;
        word-wrap: break-word;
        font-family: 'Montserrat',sans-serif;
        text-align: center;
        color : rgb(33, 100, 226);
    }

    p {
        max-width: 15.5vw;
        word-wrap: break-word;
        color: black;
        font-size: medium;
        font-family:'Caveat Brush', cursive;
    }
    button {
        background-color: red;
        border : none;
        color : white;
        transition: 1s;
        margin-bottom: 1vh;
    }
    button:hover {
        background-color : white;
        color : red;
    }
    
}

@keyframes appear {
    0% {
        opacity: 0;
        top: 35px;
    }
    100% {
        opacity: 1;
        top : 0;
    }
}
.del {
    opacity: 0;
    transform: scale(0);
}
.nodel {
    opacity: 1;
}
