body {
    margin : 0;
    padding : 0;
}

.wrapper {
    
    .page {
        display: flex;
        padding-top: 15vh;
        .wrapper {
            display: grid;
            grid-template-columns: repeat(3,1fr);
            grid-gap: 2vh;
            padding: 2vh 2vh;
            position: relative;
            width : 78vw;
            transition: 1s;
        }
        .wideWrapper {
            display: grid;
            grid-template-columns: repeat(3,1fr);
            grid-gap: 2vh;
            padding: 2vh 2vh;
            position: relative;
            width : 100vw;
            transition: 1s;
        }
        .AddingForm {
            display: block;
            position: fixed;
            z-index: 999;
            margin-left: 74vw;
            margin-top : 20vh;
            transition: 1s;
            animation: appear 1s;
        }
        .hiddenForm {
            display: block;
            position: fixed;
            z-index: 999;
            margin-top : 20vh;
            margin-left: 200vw;
            transition: 1s;
        }
       
    }
    .btn {
        animation: appear 1s;
        width : 10vw;
        height : 5vh;  
        font-size:larger;
        border : none;
        background-color: white ;
        color : rgb(33, 100, 226);
        border : rgb(33, 100, 226) solid 2px;
        box-sizing: border-box;
        transition: 1s;
        margin-left: 87vw;
        margin-top: 2vh;
        position: fixed;
        z-index: 1000;
        top : 2vh;
    }
    .btn:hover {
        background-color: rgb(33, 100, 226);
        color : white;
    }
    
}
@keyframes appear {
    0%{
        opacity : 0;
    }
    100% {
        opacity : 1;
    }
}
