@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
.form {
    form {
        display: grid;
        grid-template-rows: repeat(2);
        border : solid gray 1px;
        width : 22vw;
        height : 60vh;

        h2 {
           width : 20vw; 
           margin-left : 4.5vw; 
           color : rgb(33, 100, 226);
           font-family: 'Montserrat', sans-serif;
        }
        @media screen and (min-width : 1600px){
            h2 {
                margin-left: 11vh;
            }
        }
        @media screen and (min-width : 3000px){
            h2 {
                font-size: 75px;
                margin-left : 7.5vh;
            }
        }
        @media screen and (min-width : 1000px ) and (max-width:1280px){
            h2 {
                margin-left : 4.25vw;
            }
        }
       
        input {
            width : 20vw; 
            margin-left : 2vh;
        }
        textarea {
            width : 20vw;
            height : 30vh;
            max-width: 20vw;
            max-height: 25vh;
            margin-left : 2vh;
        } 
        .b1 {
            width : 20vw;
            height : 10vh;  
            font-size: larger;
            border : none;
            background-color: white ;
            color : rgb(33, 100, 226);
            border : rgb(33, 100, 226) solid 2px;
            box-sizing: border-box;
            transition: 1s;
            margin-left : 2vh;
        }
        .b1:hover {
            background-color: rgb(33, 100, 226);
            color : white;
        }
          
       
    }
}
